.projects-container,
.all-projects-container,
.added-projects-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.projects-container {
  padding-top: 40px;
}

.projects {
  font-family: "Playfair Display";
  font-size: 32px;
  font-weight: 700;
  letter-spacing: 0em;
  text-align: center;
  color: #25282b;
}

.project-card {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.all-projects-container {
  width: 80%;
}

.project-card {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 50px;
}

.project-details {
  flex: 3;
  margin: 20px;
  text-align: left;
}

.project-name {
  font-family: "Playfair Display";
  font-size: 28px;
  font-weight: 700;
  line-height: 60px;
  letter-spacing: 0em;
  text-align: left;
  color: #25282b;
  margin: 0;
  padding: 0;
}

.project-desc {
  font-family: Nunito;
  font-size: 18px;
  font-weight: 400;
  line-height: 27px;
  letter-spacing: 0em;
  text-align: left;
  color: #828282;
}

.link {
  text-decoration: none;
  color: #000;
}

.view-btn {
  width: 150px;
  height: 43px;
  padding: 6px 18px;
  border-radius: 24px;
  border: 1px solid #25282b;
  color: #25282b;
  background: none;
  cursor: pointer;
  font-size: 16px;
}

.view-btn:hover {
  background-color: #fdc435;
  color: #fff;
  border: none;
}

.project-image {
  width: 450px;
  height: 300px;
  flex: 2;
}

.added-projects-ul {
  list-style-type: none;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.project-li {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.add {
  text-align: center;
}

@media screen and (max-width: 567px) {
  .project-card {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .project-name,
  .project-desc {
    text-align: center;
  }

  .project-image {
    order: -1;
    flex: none;
    width: 100%;
    height: 100%;
  }

  .project-details {
    text-align: center;
  }
}
